import React from "react";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import Layout from "@components/layout";
import Seo from "@components/seo";
import BreadCrumb from "@components/common/breadcrumb";
import Privacy from "@screens/Privacy-Policy";

const PrivacyPolicy = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Privacy Policy", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <Privacy db={db} />
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const logoObj = data?.allImageSharp?.edges?.find((image) => {
    return image?.node?.gatsbyImageData?.images?.fallback?.src?.includes(
      "new_logo.png"
    );
  });
  return (
    <Seo
      title="Privacy Policy | Justtry Technologies"
      metaTitle="Privacy Policy | Justtry Technologies"
      description="Safeguard user trust with Justtry Technologies' advanced Privacy Policy solutions. Ensure GDPR compliance, fortify data protection, and establish online privacy with our expert legal terms. Elevate your platform's trustworthiness with our cutting-edge privacy solutions."
      image={logoObj?.node?.gatsbyImageData?.images?.fallback?.src}
    ></Seo>
  );
};

export default PrivacyPolicy;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    allImageSharp {
      edges {
        node {
          gatsbyImageData
          id
        }
      }
    }
  }
`;
